<template>
  <section id="sqltools">
      <el-row>
        <el-col :span="24">
            <el-collapse v-model="activeName" accordion>
                <el-collapse-item title="SQL Convert DSL" name="1">
                    <el-row>
                        <el-col :span="24">
                            <el-input type="textarea" :rows="5" placeholder="" v-model="textarea"></el-input>
                        </el-col>
                        <el-col :span="24">
                            <div style = "text-align:right;">
                                <el-button type="info" v-on:click="sqlExplain">Explain</el-button>
                            </div>
                        </el-col>
                        <el-col :span="24">
                            <div style = "color:red;">
                                {{currentError}}
                            </div>
                        </el-col>
                        <el-col :span="24">
                            <div style="display:none;" id="dslBody">
                                <el-input type="textarea" :rows="5" placeholder="" v-model="dslBody"></el-input>
                            </div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title=" Feedback" name="2">
                    <div></div>
                    <div></div>
                </el-collapse-item>
                <el-collapse-item title=" Efficiency" name="3">
                    <div></div>
                    <div></div>
                    <div></div>
                </el-collapse-item>
                <el-collapse-item title=" Controllability" name="4">
                    <div></div>
                    <div></div>
                </el-collapse-item>
            </el-collapse>
        </el-col>
    </el-row>
  </section>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'sqltools',
  data: function () {
    return {
      textarea: 'SELECT * FROM myindex',
      activeName: 1
    }
  },
  computed: {
    ...mapGetters([
      'dslBody',
      'sqlBody',
      'currentError',
      'serverhost'
    ])
  },
  created: function () {
  },
  mounted () {
  },
  methods: {
    sqlExplain () {
      var tmpVal = {url: '/_sql2dsl', sql: this.textarea, host: this.serverhost}
      this.$store.dispatch('ConvertSQLToDSL', tmpVal)
      document.getElementById('dslBody').style.display = 'block'
    }
  }
}
</script>

<style>
</style>




