<template>
  <section id="indices-view">
    <el-row :gutter="10">
      <el-col :xs="24" :sm="24" :md="24" :lg="24"> 
  <el-input placeholder="" icon="search" v-model="inputIndexName" :on-icon-click="handleIconClick">
  </el-input>
        <el-table :data="list.slice(0, showLen)" border style="width: 100%">
            <el-table-column prop="index" label="Index" width="220" sortable></el-table-column>
            <el-table-column prop="docs" label="" sortable></el-table-column>
            <el-table-column prop="size" label="Store Size" sortable></el-table-column>
            <el-table-column prop="prisize" label=""></el-table-column>
            <el-table-column prop="health" label=""></el-table-column>
            <el-table-column prop="uuid" label="UUID"></el-table-column>
            <el-table-column prop="delete" label=""></el-table-column>
            <el-table-column prop="status" label="Status"></el-table-column>
            <el-table-column label="">
              <template scope="scope">
                <el-popover ref="popover{{$index}}" placement="top" width="160" v-model="scope.row.visible" trigger="click">
                  <p>Are you sure?</p>
                  <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="scope.row.visible = false"></el-button>
                    <el-button type="primary" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
                  </div>
                </el-popover>
                <el-button size="small" type="danger" v-popover:popover{{$index}} ></el-button>
              </template>
            </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <p style="text-align:center;" v-if="indexList.length > showLen" v-on:click="addShowLen"><a style="margin:0 auto;">...</a></p>
  </section>
</template>


<script>
  import {
    mapGetters
  } from 'vuex'
  export default {
    name: 'indices-view',
    data: function () {
      return {
        showLen: 20,
        inputIndexName: '',
        list: []
      }
    },
    computed: {
      ...mapGetters([
        'indexList',
        'serverhost',
        'DeleteIndexSuccess',
        'DeleteIndexError'
      ])
    },
    watch: {
      DeleteIndexSuccess () {
        this.$message.success(this.DeleteIndexSuccess)
      },
      DeleteIndexError () {
        this.$message.error(this.DeleteIndexError)
      },
      indexList () {
        this.list = this.indexList
      }
    },
    methods: {
      filterItems (query) {
        var arr = this.indexList.filter(function (el) {
          return el['index'].toLowerCase().indexOf(query.toLowerCase()) > -1
        })
        return arr
      },
      handleIconClick () {
        if (this.inputIndexName === '') {
          this.list = this.indexList
          return false
        }
        this.list = this.filterItems(this.inputIndexName)
      },
      addShowLen () {
        this.showLen = this.showLen + 10
      },
      handleDelete (index, row) {
        row.visible = false
        var tmp = {'url': '/_indices/delete', 'index': row.index, host: this.serverhost}
        this.$store.dispatch('DeleteIndexByName', tmp)
      }
    },
    created: function () {
      this.list = this.indexList
    }
  }

</script>

<style>


</style>



